export default {
    namespaced: true,
    state: {
        programmes: []
    },
    mutations: {
        SET_PROGRAMMES: (state, data) => {state.programmes = data},
        ADD_PROGRAMME: (state, programme) => {state.programmes.push(programme)},
        REMOVE_PROGRAMME: (state, data) => {state.programmes=(data!=undefined)?[]:state.programmes},
        ACTIVATE_PROGRAMME: (state, id) => {
            state.programmes.map(ele=>{
                if(ele.id==id)
                    ele.status="ACTIVE"
                return ele
            })
        }
    },
    actions: {
        addProgramme({ commit }, programme) {
            this.$axios.post('/teacher/programmes/register', programme )
            .then(function (response) {
                commit("ADD_PROGRAMME", response.data)
            })
            .catch(function (error) {
                console.log(error)
            })
            .finally(function () {
                // always executed
            });
        },
        removeProgramme({ commit }, id) {
            this.$axios.delete(`/teacher/programmes/${id}`)
            .then(function (response) {
                commit("REMOVE_PROGRAMME", response.data)
            })
            .catch(function (error) {
                console.log(error)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchProgrammes({ commit }) {
            this.$axios.get('/admin/programmes/all')
            .then(function (response) {
                commit("SET_PROGRAMMES",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchTeacherProgrammes({ commit }, id) {
            this.$axios.get(`/teacher/${id}/programmes`)
            .then(function (response) {
                commit("SET_PROGRAMMES",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        activateProgramme({ commit }, id) {
            this.$axios.put(`/admin/programmes/${id}/activate`)
            .then(function (response) {
                commit("ACTIVATE_PROGRAMME", response.data)
            })
            .catch(function (error) {
                console.log(error)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchScholarProgrammes({ commit }, id) {
            this.$axios.get(`/scholar/scholar/${id}/programmes`)
            .then(function (response) {
                commit("SET_PROGRAMMES",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
        
    }
}