export default {
    namespaced: true,
    state: {
      error: ''
    },
    mutations: {
      SET_ERROR: (state, error) => (state.error = error),
      CLEAR_ERROR: state => (state.error = '')
    },
    actions: {
        fetchError({ commit }, msg){
            commit("SET_ERROR", msg)
        },
        removeError({ commit }){
            commit("CLEAR_ERROR")
        }
    }
}