export default {
    namespaced: true,
    state: {
      teacher: {}
    },
    mutations: {
      SET_TEACHER: (state, teacher) => (state.teacher = teacher),
      CLEAR_TEACHER: state => (state.teacher = {})
    },
    actions: {
        fetchTeacher({ commit }, id){
            this.$axios.get(`/teacher/teacher/${id}`)
            .then(function (response) {
                var teacher = response.data
                teacher.password=''
                teacher.passwordRepeat=''
                commit("SET_TEACHER", teacher)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        updateTeacher({ commit }, teacher){
            this.$axios.post(`/teacher/teacher/${teacher.id}`, teacher)
            .then(function (response) {
                var teacher = response.data
                teacher.password=''
                teacher.passwordRepeat=''
                commit("SET_TEACHER", teacher)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
  };