export default {
    namespaced: true,
    state: {
      scholar: {}
    },
    mutations: {
      SET_SCHOLAR: (state, scholar) => (state.scholar = scholar),
      CLEAR_SCHOLAR: state => (state.scholar = {})
    },
    actions: {
        fetchScholar({ commit }, id){
            this.$axios.get(`/scholar/scholar/${id}`)
            .then(function (response) {
                var scholar = response.data
                scholar.password=''
                scholar.passwordRepeat=''
                commit("SET_SCHOLAR", scholar)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        updateScholar({ commit }, scholar){
            this.$axios.post(`/scholar/scholar/${scholar.id}`, scholar)
            .then(function (response) {
                var scholar = response.data
                scholar.password=''
                scholar.passwordRepeat=''
                commit("SET_SCHOLAR", scholar)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
  };