export default {
    namespaced: true,
    state: {
        chartDataPieDepartments: []
    },
    mutations: {
        SET_CHART_DATA: (state, data) => {
            state.chartDataPieDepartments = data
        }
    },
    actions: {
        async fetchChartDataPieDepartments({ commit }) {
            try {
                const response = await this.$axios.get(`http://localhost:8077/admin/statistics/departments/scholars/all`)
                commit("SET_CHART_DATA", response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}