export default {
    namespaced: true,
    state: {
        programmePoints: []
    },
    mutations: {
        SET_PROGRAMME_POINTS: (state, data) => {
            state.programmePoints=data
        },
        ADD_PROGRAMME_POINT: (state, data) => {
            state.programmePoints.push(data)
        },
        REMOVE_PROGRAMME_POINT: (state, data) => {
            state.programmePoints=state.programmePoints.filter(ele=>ele.id!=data)
        },
        EDIT_PROGRAMME_POINT: (state, data) => {
            state.programmePoints=state.programmePoints.map(ele => (ele.id == data.id) ? data : ele)
        },
    },
    actions: {
        async fetchProgrammePoints({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/programmes/${id}/points`)
                commit("SET_PROGRAMME_POINTS", response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async fetchScholarProgrammePoints({ commit }, id) {
            try {
                const response = await this.$axios.get(`/scholar/programmes/${id}/points`)
                commit("SET_PROGRAMME_POINTS", response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async addProgrammePoint({ commit }, data) {
            try {
            const response = await this.$axios.post(`/teacher/programmes/${data.id}/point`, data.point)
            commit("ADD_PROGRAMME_POINT", response.data)
            } catch(error) {
                console.log(error)
            }
            /*.then(function (response) {
                commit("ADD_PROGRAMME_POINT", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });*/
        },
        async deleteProgrammePoint({ commit }, data) {
            this.$axios.delete(`/teacher/programmes/point/${data.id}?pointId=${data.pointId}`)
            .then(function (response) {
                commit("REMOVE_PROGRAMME_POINT", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        async editProgrammePoint({ commit }, point) {
            this.$axios.put(`/teacher/programmes/point`, point)
            .then(function (response) {
                commit("EDIT_PROGRAMME_POINT", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
    }
}